          content: '2313123123123123131212'
        },
        {
          title: '问题2',
          content: '2313123123123123131212'
        },
        {
          title: '问题3',
          content: '2313123123123123131212'
        },
        {
          title: '问题4',
          content: '2313123123123123131212'
        },
        {
          title: '问题5',
          content: '2313123123123123131212'
        },
        {
          title: '问题6',
          content: '2313123123123123131212'
        }
      ]
    }
  },
  computed: {
  },
  mounted () {
  },
  methods: {
    onClickLeft () {
      this.$router.go(-1)
    }
  }
}
</script>



